body {
  background-color: rgb(0, 0, 0);
}
* {
  color: rgb(255, 255, 255);
  font-weight: 600;
}

.Controller {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 420px;
  z-index: 1;
}
/*  *  *  *  *  *  *  *  *  */
.ContainerCtr {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 0.8fr 1.1fr 1.1fr 0.7fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "CtrA CtrB CtrC CtrD";
  height: 100%;
  /* box-shadow: inset 0 30px 8px rgb(79, 79, 79), 0 9px 10px rgb(180, 180, 180, 1); */
}
.List {
  position: relative;
  top: 420px;
  z-index: 0;
  background-color: navy;
  color: white;
  font-size: 2em;
  font-weight: 600;
  height: 72vh - 26px;
  overflow-y: hidden;
}
/*  *  *  *  * Table *  *  *  *  */
.TableContainer {
  top: 420px;
  height: 100%;
  position: relative;
}
.TheadTable {
  top: 435px;
  position: fixed;
  z-index: 9999;
  width: 100vw;
  color: rgb(50, 50, 50);
  height: 35px;
  /* border-top: 4px solid #777777; */
}
.TheadTable th {
  background-color: rgb(120, 120, 120);
  color: #2f2f2f;
  top: -17px;
  font-size: 14px;
}
.TBodyTable {
  position: relative;
  margin: 0;
  height: 100%;
  border-collapse: collapse;
}
.index {
  height: 32px;
  font-size: 0.6em;
  padding: 0px;
  position: relative;
  /* border-right: 2px solid rgb(150, 150, 150); */
  text-align: center;
  box-shadow: inset 0 -2px 2px rgb(55, 55, 55);
}
#indexYear {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexMovie {
  width: 14vw;
  border-radius: 0px 0px 8px 8px;
}
#indexDialogue {
  width: 52vw;
  border-radius: 0px 0px 8px 8px;
}
#indexActor {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexDirector {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexSource {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexScenario {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexAdaptation {
  width: 4vw;
  border-radius: 0px 0px 8px 8px;
}
#indexClip {
  width: 5vw;
  border-radius: 0px 0px 8px 8px;
}
#indexCommentary {
  width: 5vw;
  border-radius: 0px 0px 8px 8px;
}
.ListData {
  font-size: 1em;
  line-height: 1.89em;
  /* padding: 0px 10px; */
  padding: 0px;
  color: white;
  position: relative;
  border-bottom: 2px solid rgb(50, 50, 50);
  text-align: center;
  word-break: keep-all;
  padding: 20px 0px 20px 0px;
}
.ListContainer tr:hover {
  background-color: rgb(53, 53, 53);
  cursor: pointer;
}
.ListContainer tr:hover > .ListData {
  color: white;
}
.Year {
  width: 4.2vw;
}
.Movie {
  width: 14.6vw;
}
.Dialogue {
  width: 54.5vw;
}
.Actor {
  width: 4.3vw;
}
.Director {
  width: 4.3vw;
}
.Source {
  width: 4.3vw;
}
.Scenario {
  width: 4.3vw;
}
.Adaptation {
  width: 4.3vw;
}
.Clip {
  width: 5.2vw;
}
.DataResult {
  color: #707070;
  text-align: center;
  /* border-top: 3px solid #2e2e2e; */
  padding: 50px 0 20px;
  font-size: 20px;
  line-height: 32px;
  background-color: #1c1c1c;
  /* position: fixed; */
  width: 100vw;
  margin: 0;
  top: 450px;
}
