.SwitchOuter {
  grid-area: Switch;
  margin: 60px 2px 0px 2px;
  position: relative;
  display: flex;
  overflow: hidden;
  height: 80px;
}
.BtnReset {
  border-radius: 10px;
  background-color: rgb(28, 28, 28);
  border: 0px solid #000;
  color: rgb(0, 0, 0);
  padding: 15px;
  height: 70px;
  width: 180px;
  position: relative;
  box-shadow: inset 0 2px 3px gray, 0px 4px 2px black;
  /* animation: flicker1 1s infinite 0.3s; */
  border: 2px solid black;
}
.BtnReset:hover {
  background-color: rgb(67, 67, 67);
  cursor: pointer;
}

.ResetText {
  top: 18px;
  left: 22px;
  color: #d7d7d7;
  font-size: 24px;
  display: block;
  position: absolute;
  width: 130px;
  /* border: 3px solid; */
}
.BtnDarkLight {
  height: 70px;
  border-radius: 15px;
  background: rgb(185, 185, 185);
  color: rgb(20, 20, 20);
  padding: 20px;
  font-size: 1em;
  font-weight: 600;
  line-height: 1.1em;
}

.DesignEditor {
  grid-area: DesignEditor;
  width: 370px;
  height: 265px;
  padding-top: 25px;
  box-sizing: border-box;
  display: flex;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  z-index: 9999;
  /* border: 2px solid #353535; */
  border-radius: 12px;
  margin: 5px 0px 0px 0px;
}
.EditorWrap {
  display: flex;
}
.Editor,
.EditorBGColor {
  width: 65px;
  height: 204px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  border: 2px solid rgb(0, 0, 0);
  background-color: #a7a7a7;
  padding: 0px;
  position: relative;
  z-index: 999;
  border-radius: 12px;
  top: 12px;
  box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.3),
    0 2px 20px rgba(255, 255, 255, 0.3);
  margin-right: 10px;
}

.EditorSubtitle {
  font-family: "Gothic A1", sans-serif;
  font-size: 16px;
  color: #353535;
  padding: 0 6px;
  position: absolute;
  z-index: 99999;
  background: #b3b3b3;
  top: 132px;
  left: 12px;
}
.FontSubtitle {
  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  color: #353535;
  position: absolute;
  z-index: 99999;
  top: 132px;
  left: 64px;
  padding: 4px 10px;
  border-radius: 12px;
  background: #393939;
  color: #c4c4c4;
}
.SizeSubtitle {
  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  color: #353535;
  position: absolute;
  z-index: 99999;
  top: 132px;
  left: 230px;
  padding: 4px 10px;
  border-radius: 12px;
  background: #393939;
  color: #c4c4c4;
}
.FontBgSubtitle {
  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  position: absolute;
  z-index: 99999;
  top: 132px;
  left: 312px;
  padding: 4px 10px;
  border-radius: 12px;
  background: #393939;
  color: #c4c4c4;
}

.Editor .Scope,
.EditorBGColor .Scope {
  font-size: 0.7em;
  font-weight: 800;
  padding: 0 4px;
  margin: 4px;
  text-align: center;
  background: rgb(20, 20, 20);
  color: rgb(114, 114, 114);
  border-radius: 5px;
}
.EditorInput {
  transform: rotateZ(-90deg);
  margin: 58px 0 26px 0;
  width: 9em;
  appearance: none;
  background: #ffffff00;
  cursor: pointer;
  z-index: 999999999;
}
.BGColorInput {
  transform: rotateZ(-90deg);
  margin: 90px 0 26px 0;
  width: 13em;
  appearance: none;
  background: #ffffff00;
  cursor: pointer;
  z-index: 999999999;
}
.EditorLabel,
.BGColorLabel {
  display: block;
  width: 94px;
  height: 35px;
  cursor: pointer;
  border-radius: 8px;
  transition: 250ms;
  transform: rotate(-90deg);
  top: -77px;
  left: 0px;
  position: relative;
}

.EditorInput::-moz-range-track,
.BGColorInput::-moz-range-track {
  background: #000000;
  box-shadow: 1px -1px 1px 0px rgb(170, 170, 170) inset;
  height: 0.6em;
  border-radius: 20px;
  border: 1px solid rgb(80, 80, 80);
}
.EditorInput::-moz-range-thumb,
.BGColorInput::-moz-range-thumb {
  -webkit-appearance: none;
  appearance: none;
  margin-top: -10px;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgb(126, 126, 126) 0%,
    rgb(121, 121, 121) 10%,
    rgba(0, 0, 0, 1) 90%,
    rgb(162, 162, 162) 100%
  );
  height: 2em;
  width: 2.5em;
  border: 0px solid;
  border-radius: 0px;
  border-left: 10px solid black;
  border-right: 8px solid black;
}

@keyframes flicker1 {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
