.container {
  width: 200px;
  width: 200px;
  margin: 15px;
}

.container .origin {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 111;
  width: 2px;
  height: 2px;
  margin: -1px 0 0 -1px;
}

.de {
  -webkit-user-select: none;
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 100%;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
  background-color: transparent;
}

.de .den,
.de .dene,
.de .denem,
.de .deneme,
.de .dot {
  position: absolute;
  left: 50%;
  top: 50%;
}

.den {
  position: relative;
  width: 200px;
  height: 200px;
  margin: -100px 0 0 -100px;
  border-radius: 100%;
  box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.6),
    0 2px 20px rgba(255, 255, 255, 1);

  background: #3c3c3c;
  background: -moz-radial-gradient(
    center,
    ellipse cover,
    #888888 0%,
    #333333 100%
  );
  background: -webkit-radial-gradient(
    center,
    ellipse cover,
    #888888 0%,
    #333333 100%
  );
  background: -o-radial-gradient(
    center,
    ellipse cover,
    #888888 0%,
    #333333 100%
  );
}

.dene {
  z-index: 4;
  width: 140px;
  height: 140px;
  margin: -70px 0 0 -70px;
  border-radius: 100%;
  /* box-shadow: inset 0 2px 2px rgba(255, 255, 255, 0.4),
    0 3px 13px rgba(0, 0, 0, 0.85); */

  /* background: #f2f6f5;
  background: -moz-linear-gradient(top, #f2f6f5 0%, #cbd5d6 100%);
  background: -webkit-linear-gradient(top, #f2f6f5 0%, #cbd5d6 100%);
  background: -o-linear-gradient(top, #f2f6f5 0%, #cbd5d6 100%); */
}

.denem {
  width: 120px;
  height: 120px;
  margin: -60px 0 0 -60px;
  border-radius: 100%;

  background: #cbd5d6;
  background: -moz-linear-gradient(top, #cbd5d6 0%, #f2f6f5 100%);
  background: -webkit-linear-gradient(top, #cbd5d6 0%, #f2f6f5 100%);
  background: -o-linear-gradient(top, #cbd5d6 0%, #f2f6f5 100%);
}

.deneme {
  width: 100px;
  height: 100px;
  margin: -53px 0 0 -50px;
  border-radius: 100%;
  box-shadow: inset 0 2px 3px rgba(255, 255, 255, 0.6),
    0 8px 20px rgba(0, 0, 0, 0.3);
  background: #2f2f2f;
}

.den .switch {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  width: 98%;
  height: 98%;
  background: #fff0;
}

.den .switch label {
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 70px;
  margin-top: -35px;
  -moz-transform-origin: 0% 50%;
  -webkit-transform-origin: 0% 50%;
  -o-transform-origin: 0% 50%;
  cursor: pointer;
}

.den .switch label:after {
  content: "";
  position: absolute;
  top: 6px;
  left: 1px;
  width: 100%;
  height: 30px;
  -moz-transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
}

.den .switch label:before {
  content: "";
  position: absolute;
  bottom: 6px;
  left: 1px;
  width: 100%;
  height: 30px;
  -moz-transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.den .switch label span {
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 100%;
  font-weight: bold;
  font-size: 15px;
  line-height: 70px;
  text-align: center;
  color: #eee;
  text-shadow: 0 1px 0 #444;
}

.den .switch label:nth-child(1) {
  -moz-transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
}

.den .switch label:nth-child(1) span {
  right: 0px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  line-height: 42px;
  width: 62px;
  height: 64px;
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -o-transform: rotate(90deg);
}

.den .switch label:nth-child(2) {
  -moz-transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
}

.den .switch label:nth-child(2) span {
  right: -10px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  line-height: 70px;
  width: 62px;
  height: 68px;
  border-radius: 20px;
  /* border: 2px solid tomato; */
  -moz-transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.den .switch label:nth-child(3) {
  -moz-transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.den .switch label:nth-child(3) span {
  right: 2px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  -moz-transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
}

.den .switch label:nth-child(4) {
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -o-transform: rotate(90deg);
}

.den .switch label:nth-child(4) span {
  right: 2px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  -moz-transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
}

.den .switch label:nth-child(5) {
  -moz-transform: rotate(150deg);
  -webkit-transform: rotate(150deg);
  -o-transform: rotate(150deg);
}

.den .switch label:nth-child(5) span {
  right: 2px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  -moz-transform: rotate(-150deg);
  -webkit-transform: rotate(-150deg);
  -o-transform: rotate(-150deg);
}

.den .switch label:nth-child(6) {
  -moz-transform: rotate(210deg);
  -webkit-transform: rotate(210deg);
  -o-transform: rotate(210deg);
}

.den .switch label:nth-child(6) span {
  right: 2px;
  color: rgb(205, 205, 205);
  font-size: 22px;
  -moz-transform: rotate(-210deg);
  -webkit-transform: rotate(-210deg);
  -o-transform: rotate(-210deg);
}
.den .switch label span:hover {
  color: red;
}
.den .switch input {
  position: absolute;

  /* visibility: hidden; */
}

/* SWITCH LIGHT */

.den .dot {
  z-index: 6;
  width: 50%;
  height: 12px;
  margin-top: -6px;
  -webkit-transform-origin: 0% 50%;
  -moz-transition: all 0.5s;
  -webkit-transform: rotate(-125deg);
}

.den .dot span {
  position: absolute;
  top: 6px;
  left: 12px;
  width: 30px;
  height: 3px;
  background: white;
}

/* SWITCH LIGHT POSITION */

.den #switch_1:checked ~ .dot {
  -moz-transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
}

.den #switch_1:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

.den #switch_2:checked ~ .dot {
  -moz-transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
}

.den #switch_2:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

.den #switch_3:checked ~ .dot {
  -moz-transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.den #switch_3:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

.den #switch_4:checked ~ .dot {
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -o-transform: rotate(90deg);
}

.den #switch_4:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

.den #switch_5:checked ~ .dot {
  -moz-transform: rotate(150deg);
  -webkit-transform: rotate(150deg);
  -o-transform: rotate(150deg);
}

.den #switch_5:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

.den #switch_6:checked ~ .dot {
  -moz-transform: rotate(210deg);
  -webkit-transform: rotate(210deg);
  -o-transform: rotate(210deg);
}

.den #switch_6:checked ~ .dot span {
  -webkit-transform: rotate(0deg);
}

/* LINE */

.den hr.line {
  z-index: 1;
  position: absolute;
  top: 50%;
  width: 100%;
  height: 0;
  margin-top: -2px;
  border-width: 2px 0;
  border-style: solid;
  border-top-color: #3c3d3f;
  border-bottom-color: #717171;
}

.den hr.line:nth-child(1) {
  -moz-transform: rotate(-60deg);
  -webkit-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
}

.den hr.line:nth-child(2) {
  -moz-transform: rotate(60deg);
  -webkit-transform: rotate(60deg);
  -o-transform: rotate(60deg);
}

.den #switch_1:checked ~ .dot {
  -moz-transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
}

.den #switch_2:checked ~ .dot {
  -moz-transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
}

.den #switch_3:checked ~ .dot {
  -moz-transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -o-transform: rotate(30deg);
}

.den #switch_4:checked ~ .dot {
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -o-transform: rotate(90deg);
}

.den #switch_5:checked ~ .dot {
  -moz-transform: rotate(150deg);
  -webkit-transform: rotate(150deg);
  -o-transform: rotate(150deg);
}

.den #switch_6:checked ~ .dot {
  -moz-transform: rotate(210deg);
  -webkit-transform: rotate(210deg);
  -o-transform: rotate(210deg);
}
