.HelpModal {
  z-index: 1000000;
  position: relative;
}
.HelpModalContainer {
  position: fixed;
  top: 0;
  z-index: 1000000;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}
.HelpTitle {
  text-align: center;
  padding: 30px;
}
.HelpDecade,
.HelpCharacter,
.HelpKeyword,
.HelpPeople,
.HelpDesign,
.HelpText {
  border: 2px dotted rgb(255, 255, 255);
}

.HelpDecade {
  width: 400px;
  height: 66px;
  position: absolute;
  top: 45px;
  left: 25px;
  padding: 40px 30px 20px 30px;
  border-radius: 60px;
  text-align: center;
  font-size: 20px;
}

.HelpDecade span {
  font-size: 50px;
  position: absolute;
  top: 45px;
  left: 80px;
  animation: bounce2 2s ease infinite;
}
.HelpDecade p {
  animation: bounce2 2s ease infinite;
}
/*  **********  */
.HelpCharacter {
  width: 400px;
  height: 112px;
  position: absolute;
  top: 186px;
  left: 25px;
  padding: 71px 30px 20px 30px;
  border-radius: 60px;
  text-align: center;
  font-size: 20px;
}
.HelpCharacter span {
  font-size: 50px;
  position: absolute;
  top: 60px;
  left: 80px;
  animation: bounce2 2s ease infinite;
}
.HelpCharacter p {
