}
/*  **********  */
.HelpKeyword {
  width: 528px;
  height: 190px;
  position: absolute;
  top: 33px;
  left: 515px;
  padding: 160px 30px 20px 30px;
  border-radius: 60px;
  text-align: center;
  font-size: 20px;
}
.HelpKeyword span {
  font-size: 50px;
  position: absolute;
  top: 150px;
  left: 120px;
  animation: bounce2 2s ease infinite;
}
.HelpKeyword p {
  animation: bounce2 2s ease infinite;
}
/*  **********  */
.HelpPeople {
  width: 617px;
  height: 190px;
  position: absolute;
  top: 33px;
  left: 1143px;
  padding: 160px 30px 20px 30px;
  border-radius: 60px;
  text-align: center;
  font-size: 20px;
}
.HelpPeople span {
  font-size: 50px;
  position: absolute;
  top: 150px;
  left: 120px;
  animation: bounce2 2s ease infinite;
}
.HelpPeople p {
  animation: bounce2 2s ease infinite;
