/*  *  *  *  *  Decade  *  *  *  *  */
.DecadeSection {
  margin: 60px 0px 0px 0px;
  position: relative;
  border: 2px solid #353535;
  border-radius: 12px;
  width: 464px;
  height: 90px;
  padding: 14px 0px 0px 8px;
}

.DecadeSection .subtitle {
  background-color: #b3b3b3;
  color: #353535;
  position: absolute;
  top: -26px;
  left: 24px;
  padding: 0 6px;
  font-size: 16px;
}

.Decades {
  z-index: 9;
}
.DecadeWrap {
  width: 450px;
  margin: 2px 2px;
  border-radius: 5px;
  display: flex;
  background-color: rgb(0, 0, 0);
  padding: 2px;
}
.DecadeWrapInner {
  width: 100%;
  margin: 0px;
  display: flex;
  background-color: rgb(130, 130, 130);
  border: 2px solid black;
  border-radius: 10px;
  justify-content: center;
}

.Decade {
  width: 42px;
  height: 56px;
  position: relative;
  margin: -5px 2px 15px 2px;
}
.Decade .Param {
  font-size: 0.9em;
  font-weight: 800;
  padding: 4px 6px 0px;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: rgb(200, 143, 143);
  border-radius: 8px;
  z-index: 99999999;
}
.DecadeLabel {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 6px;
  left: 6px;
  cursor: pointer;
  background: linear-gradient(to bottom, #333333 0%, #464646 40%, #454545 100%);
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5),
    0px 1px 0px rgb(167, 167, 167);
}
.DecadeLabel:after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 2px;
  left: 2px;
  box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0, 0, 0, 0.5);
  opacity: 0;
}

.DecadeLabel:hover::after {
  opacity: 0.6;
}
.DecadeInput[type="checkbox"] {
  visibility: hidden;
}
.DecadeInput[type="checkbox"]:checked + label:after {
  opacity: 1;
  animation: flicker 0.2s infinite 0.3s;
}
.switch {
  background-color: black;
  width: 150px;
  height: 195px;
  border-radius: 5px;
  padding: 0px;
  perspective: 700px;
}
.switch input {
  display: none;
}
.switch input:checked + .button {
  transform: translateZ(20px) rotateX(25deg);
  box-shadow: 0 -10px 20px #ff1818;
}
.switch input:checked + .button .light {
  animation: flicker 0.2s infinite 0.3s;
}
.switch input:checked + .button .shine {
  opacity: 1;
}
.switch input:checked + .button .shadow {
  opacity: 0;
}
.switch input:checked + .button .Param {
  color: rgb(95, 7, 7);
}
.switch .button {
  transition: all 0.3s cubic-bezier(1, 0, 1, 1);
  transform-origin: center center -20px;
  transform: translateZ(20px) rotateX(-25deg);
  transform-style: preserve-3d;
  background-color: #9b0621;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  background: linear-gradient(
    #980000 0%,
    #6f0000 30%,
    #6f0000 70%,
    #980000 100%
  );
  background-repeat: no-repeat;
}
.switch .button::before {
  content: "";
  background: linear-gradient(
        rgba(255, 255, 255, 0.8) 10%,
        rgba(255, 255, 255, 0.3) 30%,
        #650000 75%,
        #320000
      )
      50% 50%/97% 97%,
    #b10000;
  background-repeat: no-repeat;
  width: 100%;
  height: 24px;
  transform-origin: top;
  transform: rotateX(-90deg);
  position: absolute;
  top: 0;
}
.switch .button::after {
  content: "";
  background-image: linear-gradient(#650000, #320000);
  width: 100%;
  height: 50px;
  transform-origin: top;
  transform: translateY(50px) rotateX(-100deg);
  position: absolute;
  bottom: 0;
  box-shadow: 0px 10px 4px black, 0 10px 20px 0px rgba(0, 0, 0, 0.2);
}
.switch .light {
  opacity: 0;
  animation: light-off 1s;
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(
    #ff743e 10%,
    #ff1818 50%,
    #ff1818 100%,
    transparent 100%
  );
}
.switch .dots {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(transparent 30%, rgba(101, 0, 0, 0.7) 70%);
  background-size: 0px 0px;
}

.switch .shine {
  transition: all 0.3s cubic-bezier(1, 0, 1, 1);
  opacity: 0.3;
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(white, transparent 3%) 50% 50%/97% 97%,
    linear-gradient(
        rgba(255, 255, 255, 0.5),
        transparent 50%,
        transparent 80%,
        rgba(255, 255, 255, 0.5)
      )
      50% 50%/97% 97%; */
  background-repeat: no-repeat;
}
.switch .shadow {
  transition: all 0.3s cubic-bezier(1, 0, 1, 1);
  opacity: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(transparent 80%, rgba(0, 0, 0, 0.8));
  background-repeat: no-repeat;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@keyframes light-off {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
}
