.CtrC .subtitle {
  background-color: #b3b3b3;
  color: #353535;
  top: -98px;
  left: 30px;
  padding: 0 6px;
  font-size: 18px;
  position: absolute;
}
.PeopleSection {
  border: 2px solid #353535;
  width: 106px;
  height: 328px;
  top: 60px;
  border-radius: 12px;
  position: relative;
  cursor: pointer;
}
.FilterActor {
  grid-area: FilterActor;
}
.FilterDirector {
  grid-area: FilterDirector;
}
.FilterSource {
  grid-area: FilterSource;
}
.FilterScenario {
  grid-area: FilterScenario;
}
.FilterAdaptation {
  grid-area: FilterAdaptation;
}
.FilterActor,
.FilterDirector,
.FilterSource,
.FilterScenario,
.FilterAdaptation {
  position: absolute;
  width: 105px;
  font-size: 14px;
  top: 70px;
  height: 140px;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
}

.PeopleWrap {
  position: relative;
  overflow-y: scroll;
  width: 84px;
  top: 30px;
  height: 286px;
  margin: 0px 0px 0px 0px;
  text-align: center;
  background: #313131;
  border: 6px solid rgb(7, 7, 7);
  border-radius: 8px;
}

.PeopleContainer::-webkit-scrollbar {
  width: 10px;
  box-sizing: border-box;
}
.PeopleContainer::-webkit-scrollbar-track {
  background-color: black;
}

.PeopleContainer::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
  border-radius: 2px;
  border-top: 5px solid rgb(85, 85, 85);
  border-bottom: 5px solid rgb(85, 85, 85);
  background-image: url(../../imgs/scroll_bg.png);
  background-size: contain;
}

.PeopleContainer::-webkit-scrollbar-thumb:hover {
  background: #999999;
}
/* 체크박스 스타일 */
.checkbox-div {
  cursor: pointer;
  border-bottom: 2px solid #464646;
}
.PeopleInput .Param {
  cursor: pointer;
  font-size: 16px;
  line-height: 36px;
}
.PeopleInput .Param:hover {
  color: #ddff00;
}
.checkbox-div.checked {
  background: linear-gradient(
    0deg,
    #8ca000 0%,
    #ddff00 10%,
    #ddff00 90%,
    #9bb100 100%
  );
  box-shadow: 0px 0px 10px #ddff00;
  color: black;
  cursor: pointer;
  animation: flicker 0.2s infinite 0.3s;
  border-bottom: 6px solid #8ca000;
}
.checkbox-label.checked .Param {
  color: black;
}

/* 체크박스 숨기기 */
.checkbox-div.checked .checkbox-label input {
  display: none;
}
.hidden-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@keyframes light-off {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
}
