.WrapIntro {
  position: relative;
  /* background-color: blue; */
  width: 110px;
  height: 265px;
}
.WrapTitle {
  margin: 14px;
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(http://localhost:3000/static/media/img_title.0198f1ed12566dc853ad.png);
  top: -3px;
  position: absolute;
  z-index: 9999999999;
  width: 100px;
  left: 252px;
}
.TextIntro {
  color: black;
  margin: 82px 15px 0px 15px;
  font-size: 14px;
}
.WrapGuide {
  position: absolute;
  text-align: center;
  width: 70px;
  height: 85px;
  padding: 5px;
  border-radius: 12px;
  margin: 15px;
  /* border: 2px solid black; */
  /* box-shadow: 2px 2px 4px rgb(96, 96, 96); */
  top: -35px;
}
.WrapGuide .subtitle {
  background-color: #b3b3b3;
  color: #353535;
  top: -10px;
  left: 10px;
  padding: 0 6px;
  font-size: 16px;
}
.TextGuide {
  position: absolute;
  bottom: 0;
  left: -10px;
  height: 66px;
  width: 180px;
  border-radius: 12px;
  margin: 6px;
  font-weight: 900;
  cursor: pointer;
  font-size: 36px;
  line-height: 0px;
  background-color: rgb(182, 19, 19);
  box-shadow: inset 0 2px 3px gray, 0px 4px 2px black;
  border: 2px solid black;
}
.TextGuide:hover {
  background-color: rgb(179, 56, 56);
}
.TextGuide p {
  animation: bounce1 2s ease infinite;
  color: #d7d7d7;
}

@keyframes bounce1 {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  60% {
    transform: translateY(-3px);
  }
}
