  transition: transform 1s ease-in-out;
}

.isMouseActive {
  /* Styles for isMouseActive class */
}

.scaleUp {
  transform: scale(1.1);
}
.style-1 {
  font-family: "BookkGothic-Bd", serif;
  font-weight: 700;
}
.style-2 {
  font-family: "BookkMyungjo-Bd", serif;
  font-weight: 700;
}
.style-3 {
  font-family: "KCCImkwontaek", serif;
}
