}
.style-5 {
  font-family: "ChungjuKimSaengTTF", sans-serif;
}
.style-6 {
  font-family: "SuseongHyejeong", serif;
}

@media print {
  body * {
    visibility: hidden;
  }

  #printable-modal-content,
  #printable-modal-content * {
    visibility: visible;
  }

  #printable-modal-content {
    position: absolute;
    left: 0;
    top: 0;
  }
}

.typing-effect::before {
  content: "";
  display: block;
  width: 0;
  height: 100%;
  background-color: inherit;
  animation: typing 2s steps(40, end) forwards; /* Adjust duration as needed */
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
